import { Responder } from "../answers";

/* global window, document, Office */

declare global {
  interface Window {
    responder: Responder;
  }
}

function getFileInputElement() {
  return document.getElementById("fileInput") as HTMLInputElement;
}

function getOpenAIKeyElement() {
  return document.getElementById("openAIKey") as HTMLInputElement;
}

function getStoreKeyButtonElement() {
  return document.getElementById("btnStoreKey") as HTMLButtonElement;
}

(async () => {
  const info = await Office.onReady();
  if (info.host === Office.HostType.Excel) {
    getStoreKeyButtonElement().onclick = updateKey;
    getFileInputElement().onchange = loadConext;
    getOpenAIKeyElement().oninput = openAIKeyChange;

    const storedApiKey = await OfficeRuntime.storage.getItem("openAIKey");
    if (storedApiKey) {
      window.responder = new Responder(storedApiKey);
      getFileInputElement().disabled = false;
      getOpenAIKeyElement().placeholder = "key is already stored";
    }
  }
})();

async function updateKey() {
  const openAIKeyElement = getOpenAIKeyElement();
  const apiKey = openAIKeyElement.value;
  if (apiKey) {
    await OfficeRuntime.storage.setItem("openAIKey", apiKey);
    window.responder = new Responder(apiKey);
    getFileInputElement().disabled = false;
    openAIKeyElement.placeholder = "key is already stored";
    openAIKeyElement.value = "";
  }
}

function loadConext() {
  const contextFile = getFileInputElement();
  const reader = new FileReader();
  reader.onload = (event) => {
    if (event.type === "load") window.responder.loadContext(reader.result as string);
  };
  reader.readAsText(contextFile.files[0]);
}

function openAIKeyChange() {
  getStoreKeyButtonElement().disabled = !getOpenAIKeyElement().value;
}
